/* eslint-disable react-hooks/exhaustive-deps*/
import React, { useEffect } from 'react';
import { Spin } from 'antd';
import { ProductList, FilterArea } from '../../components';
import { MainLayout } from '../../layouts'
import { useLocation, useParams } from 'react-router-dom';
import { useSelector } from '../../redux/hooks';
import { useDispatch } from 'react-redux';
import { searchDetail } from '../../redux/productSearch/productSearch.slice';
import styles from './SearchPage.module.css';

interface MatchParams {
    keywords: string;
}

export const SearchPage: React.FC = () => {
    const loading = useSelector(state => state.searchDetail.loading);
    const error = useSelector(state => state.searchDetail.error);
    const pagination = useSelector(state => state.searchDetail.pagination);
    const productList = useSelector(state => state.searchDetail.data);

    const dispatch = useDispatch();
    const location = useLocation();
    const { keywords } = useParams<MatchParams>();
    useEffect(() => {
        dispatch(searchDetail({ nextPage: 1, pageSize: 6, keywords }))
    }, [location])
    const onPageChange = (nextPage, pageSize) => {
        dispatch(searchDetail({ nextPage, pageSize, keywords }));
    }

    if (loading) {
        return (
            <Spin
                size="large"
                style={{
                    marginTop: 200,
                    marginBottom: 200,
                    marginLeft: "auto",
                    marginRight: "auto",
                    width: "100%",
                }}
            />
        );
    }
    if (error) {
        return <div>网站出错demo：{error}</div>;
    }

    return (
        <MainLayout>
            {/* 分类过滤器 */}
            <div className={styles["product-list-container"]}>
                <FilterArea />
            </div>
            {/* 产品列表  */}
            <div className={styles["product-list-container"]}>
                <ProductList
                    data={productList}
                    paging={pagination}
                    onPageChange={onPageChange}
                />
            </div>
        </MainLayout>
    )
}