import React, { useState, useEffect } from "react";
import styles from "./Header.module.css";
import logo from "../../assets/logo.svg";
import { Layout, Typography, Input, Menu, Button, Dropdown } from "antd";
import { GlobalOutlined } from "@ant-design/icons";
import { useHistory, Link } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { RootState } from '../../redux/store';
import { changeLanguageActionCreator, addLanguageActionCreator } from "../../redux/language/languageActions";
import { useDispatch } from 'react-redux';
import { useSelector } from '../../redux/hooks';
import jw_decode, { JwtPayload as DefaultJwtPayload } from 'jwt-decode';
import { userSlice } from '../../redux/user/user.slice';

interface JwtPayload extends DefaultJwtPayload {
    username: string
}

export const Header: React.FC = () => {
    const history = useHistory();
    const { t } = useTranslation();
    const language = useSelector((state: RootState) => state.language.language);
    const languageList = useSelector((state: RootState) => state.language.languageList);
    const shoppingCartLoading = useSelector((state) => state.shoppingCart.loading);
    const dispatch = useDispatch();
    const jwt = useSelector((state) => state.user.token);
    const [username, setUsername] = useState("");
    const shoppingCartItems = useSelector((state) => state.shoppingCart.items);

    useEffect(() => {
        if (jwt) {
            const token = jw_decode<JwtPayload>(jwt);
            setUsername(token.username);
        }
    }, [jwt])

    const menuClickHandler = (e) => {
        if (e.key === 'new') {
            dispatch(addLanguageActionCreator("新语言", "new_lang"))
        } else {
            dispatch(changeLanguageActionCreator(e.key))
        }
    }
    const onLoginOut = () => {
        dispatch(userSlice.actions.loginOut());
        history.push("/");
    }
    return (
        <div className={styles["app-header"]} >
            < div className={styles["top-header"]} >
                <div className={styles.inner}>
                    <Typography.Text>{t('header.slogan')}</Typography.Text>
                    <Dropdown.Button
                        style={{ marginLeft: 15 }}
                        overlay={
                            <Menu onClick={menuClickHandler}>
                                {
                                    languageList.map(item => (
                                        <Menu.Item key={item.code}>{item.name}</Menu.Item>
                                    ))
                                }
                                <Menu.Item key={"new"}>{t('header.add_new_language')}</Menu.Item>
                            </Menu>
                        }
                        icon={<GlobalOutlined />}
                    >
                        {language === 'zh' ? '中文' : '英文'}
                    </Dropdown.Button>
                    {
                        jwt ? (<Button.Group className={styles["button-group"]}>
                            <span style={{ padding: '0px 10px' }}>
                                {t("header.welcome")}
                                <Typography.Text strong>{username}</Typography.Text>
                            </span>
                            <Button loading={shoppingCartLoading} onClick={() => history.push("/shoppingCart")}>{t('header.shoppingCart')}
                                {shoppingCartItems.length > 0 ? (`(${shoppingCartItems.length})`) : null}
                            </Button>
                            <Button onClick={onLoginOut}>{t('header.signOut')}</Button>
                        </Button.Group>) :
                            (<Button.Group className={styles["button-group"]}>
                                <Button onClick={() => history.push('/register')}>{t('header.register')}</Button>
                                <Button onClick={() => history.push('/signIn')}>{t('header.signin')}</Button>
                            </Button.Group>)
                    }
                </div>
            </div>
            <Layout.Header className={styles["main-header"]}>
                <Link to={"/"}>
                    <img src={logo} alt="logo" className={styles["App-logo"]} />
                    <Typography.Title level={3} className={styles.title}>
                        {t('header.title')}
                    </Typography.Title>
                </Link>
                <Input.Search
                    placeholder={t('header.placeholder')}
                    className={styles["search-input"]}
                    onSearch={(keywords) => history.push("/search/" + keywords)}
                />
            </Layout.Header>
            <Menu mode={"horizontal"} className={styles["main-menu"]}>
                <Menu.Item key="1">{t('header.home_page')}</Menu.Item>
                <Menu.Item key="2">{t('header.weekend')}</Menu.Item>
                <Menu.Item key="3">{t('header.group')}</Menu.Item>
                <Menu.Item key="4"> {t('header.backpack')} </Menu.Item>
                <Menu.Item key="5"> {t('header.private')} </Menu.Item>
                <Menu.Item key="6"> {t('header.cruise')} </Menu.Item>
                <Menu.Item key="7"> {t('header.hotel')} </Menu.Item>
                <Menu.Item key="8"> {t('header.local')} </Menu.Item>
                <Menu.Item key="9"> {t('header.theme')} </Menu.Item>
                <Menu.Item key="10"> {t('header.custom')} </Menu.Item>
                <Menu.Item key="11"> {t('header.study')} </Menu.Item>
                <Menu.Item key="12"> {t('header.visa')} </Menu.Item>
                <Menu.Item key="13"> {t('header.enterprise')} </Menu.Item>
                <Menu.Item key="14"> {t('header.high_end')} </Menu.Item>
                <Menu.Item key="15"> {t('header.outdoor')} </Menu.Item>
                <Menu.Item key="16"> {t('header.insurance')} </Menu.Item>
            </Menu>
        </div >
    );
};
