import React from 'react';
import { SideMenu, Carousel, ProductCollection, BusinessPartners } from '../../components'
import { MainLayout } from '../../layouts';
import { Row, Col, Typography, Spin } from 'antd';
import sideImage from '../../assets/images/sider_2019_12-09.png';
import sideImage2 from '../../assets/images/sider_2019_02-04.png';
import sideImage3 from '../../assets/images/sider_2019_02-04-2.png';
import { withTranslation, WithTranslation } from 'react-i18next';
import _ from 'lodash';
import { connect } from 'react-redux';
import {
    getRecommendProductsActionCreator
} from '../../redux/recommendProducts/recommendProductsActions';
import { RootState } from '../../redux/store';

interface State {
    loading: boolean,
    error: string | null,
    productList: any[],
}

const mapStateToProps = (state: RootState) => {
    return {
        loading: state.recommendProducts.loading,
        error: state.recommendProducts.error,
        productList: state.recommendProducts.productList,
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getRecommendProductsList: () => {
            dispatch(getRecommendProductsActionCreator())
        }
    }
}

type PropsType = WithTranslation & ReturnType<typeof mapStateToProps> & ReturnType<typeof mapDispatchToProps>

class HomePageComponent extends React.Component<PropsType, State> {
    componentDidMount() {
        this.props.getRecommendProductsList();
    }
    render() {
        const { t } = this.props;
        const { loading, error, productList } = this.props;
        if (loading) {
            return (
                <Spin
                    size="large"
                    style={{
                        marginTop: 200,
                        marginBottom: 200,
                        marginLeft: 'auto',
                        marginRight: 'auto',
                        width: '100%'
                    }}
                />
            )
        }
        if (error) {
            return (
                <div style={{ textAlign: 'center', fontSize: '20px' }}>
                    网站出错demo：{error}
                </div>
            )
        }
        return <MainLayout>
            <Row style={{ marginTop: 20 }}>
                <Col span={6}>
                    <SideMenu />
                </Col>
                <Col span={18}>
                    <Carousel />
                </Col>
            </Row>
            <ProductCollection
                title={
                    <Typography.Title level={3} type="warning">
                        {t("home_page.hot_recommended")}
                    </Typography.Title>
                }
                sideImage={sideImage}
                products={!_.isEmpty(productList) ? productList[0].touristRoutes : []}
            />
            <ProductCollection
                title={
                    <Typography.Title level={3} type="danger">
                        {t("home_page.new_arrival")}
                    </Typography.Title>
                }
                sideImage={sideImage2}
                products={!_.isEmpty(productList) ? productList[1].touristRoutes : []}
            />
            <ProductCollection
                title={
                    <Typography.Title level={3} type="success">
                        {t("home_page.domestic_travel")}
                    </Typography.Title>
                }
                sideImage={sideImage3}
                products={!_.isEmpty(productList) ? productList[2].touristRoutes : []}
            />
            <BusinessPartners />
        </MainLayout>
    }
}

export const HomePage = connect(
    mapStateToProps,
    mapDispatchToProps,
)(withTranslation()(HomePageComponent));
