import { ThunkAction } from 'redux-thunk';
import { RootState } from '../store';
import axios from 'axios';

export const FETCH_RECOMMEND_PRODUCTS_START = "FETCH_RECOMMEND_PRODUCTS_START";
export const FETCH_RECOMMEND_PRODUCTS_SUCCESS = "FETCH_RECOMMEND_PRODUCTS_SUCCESS";
export const FETCH_RECOMMEND_PRODUCTS_FAILUER = "FETCH_RECOMMEND_PRODUCTS_FAILUER";


interface FetchRecommendProductsStartAction {
    type: typeof FETCH_RECOMMEND_PRODUCTS_START,
}

interface FetchRecommendProductsSuccessAction {
    type: typeof FETCH_RECOMMEND_PRODUCTS_SUCCESS,
    payload: any,
}

interface FetchRecommendProductsFailureAction {
    type: typeof FETCH_RECOMMEND_PRODUCTS_FAILUER,
    payload: any,
}

export type RecommendProductsAction = FetchRecommendProductsStartAction | FetchRecommendProductsSuccessAction | FetchRecommendProductsFailureAction;

export const fetchRecommendProductsStartActionCreator = (): FetchRecommendProductsStartAction => {
    return {
        type: FETCH_RECOMMEND_PRODUCTS_START,
    }
}

export const fetchRecommendProductsSuccessActionCreator = (data): FetchRecommendProductsSuccessAction => {
    return {
        type: FETCH_RECOMMEND_PRODUCTS_SUCCESS,
        payload: data
    }
}

export const fetchRecommendProductsFailureActionCreator = (error): FetchRecommendProductsFailureAction => {
    return {
        type: FETCH_RECOMMEND_PRODUCTS_FAILUER,
        payload: error,
    }
}

export const getRecommendProductsActionCreator = (): ThunkAction<
    void,
    RootState,
    unknown,
    RecommendProductsAction
> => async (dispatch, getState) => {
    dispatch(fetchRecommendProductsStartActionCreator());
    try {
        const { data } = await axios.get("https://123.56.149.216:8080/api/productCollections");
        dispatch(fetchRecommendProductsSuccessActionCreator(data));
    } catch (error) {
        dispatch(fetchRecommendProductsFailureActionCreator(error.message));
    }
}


