// import { createStore, applyMiddleware } from 'redux';
import languageReducer from './language/languageReducer';
import recommendProductsReducer from './recommendProducts/recommendProductsReducer';
import { productDetailSlice } from './productDetail/productDetail.slice';
import { productSearchSlice } from './productSearch/productSearch.slice';
import { userSlice } from './user/user.slice';
import { shoppingCartSlice } from './shoppingCart/shoppingCart.slice';
import {placeOrderSlice} from './order/order.slice';
// import thunk from 'redux-thunk';
import { actionLog } from './middlewares/actionLog';
import { combineReducers, configureStore } from '@reduxjs/toolkit';
import { persistStore, persistReducer } from 'redux-persist';
import storage from 'redux-persist/lib/storage';

const persistConfig = {
    key: "root",
    storage,
    whiteList: ['user'] //reducer里面的user会保存
}

const rootReducer = combineReducers({
    language: languageReducer,
    recommendProducts: recommendProductsReducer,
    productDetail: productDetailSlice.reducer,
    searchDetail: productSearchSlice.reducer,
    user: userSlice.reducer,
    shoppingCart: shoppingCartSlice.reducer,
    order: placeOrderSlice.reducer,
})

const persistedReducer = persistReducer(persistConfig, rootReducer);

// const store = createStore(rootReducer, applyMiddleware(thunk, actionLog));
const store = configureStore({
    reducer: persistedReducer,
    middleware: (getDefaultMiddleware) => [...getDefaultMiddleware({
        serializableCheck: false
    }), actionLog],
    devTools: true,
});

const persistor = persistStore(store);

export type RootState = ReturnType<typeof store.getState>

export default { store, persistor };
