import { createSlice, PayloadAction, createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios';
import { checkout } from '../shoppingCart/shoppingCart.slice';

interface ShoppingCartState {
    loading: boolean;
    error: string | null;
    currentOrder: any;
}

const initialState: ShoppingCartState = {
    loading: false,
    error: null,
    currentOrder: null,
}

export const placeOrder = createAsyncThunk(
    "order/placeOrder",
    async (params: {
        jwt: string,
        orderId: string,
    }, thunkAPI) => {
        const { data } = await axios.post(
            `https://123.56.149.216:8080/api/orders/${params.orderId}/placeOrder`, null,
            {
                headers: {
                    Authorization: `bearer ${params.jwt}`
                }
            }
        );
        return data;
    }
)

export const placeOrderSlice = createSlice({
    name: "placeOrderSlice",
    initialState,
    reducers: {},
    extraReducers: {
        [placeOrder.pending.type]: (state) => {
            state.loading = true;
        },
        [placeOrder.fulfilled.type]: (state, action) => {
            state.currentOrder = action.payload;
            state.loading = false;
            state.error = null;
        },
        [placeOrder.rejected.type]: (state, action: PayloadAction<string | null>) => {
            state.loading = false;
            state.error = action.payload;
        },
        [checkout.pending.type]: (state) => {
            state.loading = true;
        },
        [checkout.fulfilled.type]: (state, action) => {
            state.currentOrder = action.payload;
            state.loading = false;
            state.error = null;
        },
        [checkout.rejected.type]: (state, action: PayloadAction<string | null>) => {
            state.loading = false;
            state.error = action.payload;
        }
    }
});