import { createSlice, PayloadAction, createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios';

interface ProductSearchState {
    loading: boolean;
    error: string | null;
    data: any;
    pagination: any;
}

const initialState: ProductSearchState = {
    loading: true,
    error: null,
    data: null,
    pagination: null,
}

export const searchDetail = createAsyncThunk(
    "productSearch/productSearchSlice",
    async (
        params: {
            keywords: string;
            nextPage: number | string;
            pageSize: number | string;
        },
        thunkAPI
    ) => {
        let url = `https://123.56.149.216:8080/api/touristRoutes?pageNumber=${params.nextPage}&pageSize=${params.pageSize}`;
        if (params.keywords) {
            url += `&keyword=${params.keywords}`;
        }
        const response = await axios.get(url);
        return {
            data: response.data,
            pagination: JSON.parse(response.headers["x-pagination"]),
        }
    }
)

export const productSearchSlice = createSlice({
    name: "productSearch",
    initialState,
    reducers: {},
    extraReducers: {
        [searchDetail.pending.type]: (state) => {
            state.loading = true;
        },
        [searchDetail.fulfilled.type]: (state, action) => {
            state.data = action.payload.data;
            state.pagination = action.payload.pagination;
            state.loading = false;
            state.error = null;
        },
        [searchDetail.rejected.type]: (state, action: PayloadAction<string | null>) => {
            state.loading = false;
            state.error = action.payload;
        }
    }
});